h1{
    text-align: center;
    line-height: 2;
    margin: 30px;
    font-size: 50px;
}
span{
    writing-mode: vertical-lr;
    transform: scale(-1,-1);
    font-size: 25px;
    color: rgb(0, 0, 0); 
}
.navbar{
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 70px;
  line-height: 3;
}
li{
   list-style: none;
}
.link-list{
    text-decoration: none;
    color: purple;
    font-size: 20px;
}
.link-list:hover{
    transition: ease-in;
    color: rgb(34, 0, 128);
    font-weight: 600;
    text-decoration: underline;
}

button{
    border:none;
    margin-right: 4rem;
    background-color: white;
    
}
.link-list1{
    text-decoration: none;
    color: purple;
    font-size: 20px;
    padding: 14px 25px;
}
.link-list2{
    text-decoration: none;
    color: purple;
    font-size: 20px;
    padding: 14px 25px;
}



.hr-tag{
 display: flex;
 justify-content: center;
 width: 76%;
 overflow: hidden;
 margin-left: 150px;
 
}

.topRight {
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .topImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
  }
  
  .topList {
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  .topListItem {
    margin-right: 20px;
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
  }
@media screen and (max-width: 600px){
    .navbar{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 10px;
    }
}